.careof {
    background: linear-gradient(0.61deg, #617A84 0.49%, #87A9B7 101.49%);
}
.careof__image {
    width: vw(404);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
    transition: all .6s ease;
}
.careof:hover .careof__image {
    transform: translateX(-50%) translateY(-50%) rotate(90deg);
}


@media (max-width: 1023px){
    .careof .careof__image {
        transform: translateX(-50%) translateY(-50%) rotate(90deg);
    }
}
