.preloader {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: $bgColor;
  z-index: 300;
  transition: all 0.3s ease;
  opacity: 1;
  visibility: visible;
  &.isHidden {
    opacity: 0;
    visibility: hidden;
  }
  &__in {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__image {
    width: vw(233);
    height: vw(250);
  }
  &.mobile &__image {
    width: 233px;
    height: 250px;
  }
}
