.industry-specialization {
  position: relative;
  padding-bottom: vw(144);
  z-index: 5;

  @media (max-width: 1023px) {
    padding-bottom: 75px;
  }
}

.industry-specialization__title {
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: bold;
  color: $white;
  font-size: vw(128);
  line-height: vw(144);
  margin-bottom: vw(40);

  @media (max-width: 1023px) {
    font-size: 32px;
    line-height: 40px;
    margin-bottom: 24px;
  }
}

.industry-specialization__content {
  display: grid;
  grid-template-columns: calc(50% + #{vw(40)}) calc(50% - #{vw(40)});
  grid-gap: 0 vw(10);
  padding-left: vw(44);

  @media (max-width: 1023px) {
    grid-template-columns: 1fr;
    padding-left: 16px;
    padding-right: 16px;
    grid-gap: 20px 0;
  }
}

.industry-specialization__column {
  min-width: vw(320);

  &:first-child {
    z-index: 1;
  }

  @media (max-width: 1023px) {
    min-width: 320px;

    &:first-child {
      padding-right: 0;
    }
  }
}

.industry-specialization-tab__wrapper-navigation {
  overflow: hidden;
  margin-bottom: vw(40);

  @media (max-width: 1023px) {
    margin-bottom: 16px;
  }
}

.industry-specialization-tab__navigation {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: vw(-6);

  @media (max-width: 1023px) {
    margin: -6px;
  }
}

.industry-specialization-tab__navigation-item {
  margin: vw(6);

  @media (max-width: 1023px) {
    margin: 6px;
  }
}

.industry-specialization-tab__content {
  overflow: hidden;
}

.industry-specialization-tab__grid-row-hidden {
  width: 70.58%;

  @media (max-width: 1023px) {
    width: 100%;
  }
}

.industry-specialization-tab__grid-row-scroll {
  @media (max-width: 1023px) {
    width: 100%;
    overflow: auto;

    &::-webkit-scrollbar {
      width: 1px;
      height: 1px;
    }
  }
}

.industry-specialization-tab__grid-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: vw(24);

  @media (max-width: 1023px) {
    grid-template-columns: 100px;
    grid-auto-flow: column;
    grid-auto-columns: 100px;
    grid-gap: 8px;
  }
}

.industry-specialization-tab-link-item {
  display: flex;
  background-color: #131527;
  transition: all 0.3s ease;

  &:hover {
    background-color: $mainColor;

    .industry-specialization-tab-link-item__img {
      transform: translate(-50%, -50%) scale(1.113);
    }

    .industry-specialization-tab-link-item__title {
      color: $white;
    }
  }
}

.industry-specialization-tab-link-item__wrapper-content {
  width: 100%;
  padding-bottom: 75%;
  overflow: hidden;
  position: relative;
}

.industry-specialization-tab-link-item__content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.industry-specialization-tab-link-item__img {
  position: absolute;
  top: 50%;
  left: 50%;
  max-width: 75%;
  max-height: 100%;
  object-fit: cover;
  transform: translate(-50%, -50%) scale(1);
  transition: all 0.3s ease;
  @media (max-width: 1023px) {
    max-width: 75%;
    max-height: 75%;
  }
}

.industry-specialization-tab-link-item__title {
  font-size: vw(48);
  font-weight: 700;
  color: $mainColor;
  margin-bottom: vw(4);
  transition: all 0.3s ease;
  @media (max-width: 1023px) {
    font-size: 25px;
    margin-bottom: 4px;
  }
}

.industry-specialization-tab-link-item__description {
  font-size: vw(16);
  color: $white;
  @media (max-width: 1023px) {
    font-size: 16px;
  }
}

.industry-specialization-tab-picture-item__content {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  box-shadow: 0 vw(10) vw(56) rgba(0, 38, 3, 0.08);
  border-radius: vw(4);
  overflow: hidden;

  @media (max-width: 1023px) {
    box-shadow: 0 10px 56px rgba(0, 38, 3, 0.08);
    border-radius: 4px;
  }
}

.industry-specialization-tab-picture-item__wrapper-img {
  width: 100%;
  padding-bottom: 100%;
  overflow: hidden;
  position: relative;
}

.industry-specialization-tab-picture-item__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.industry-specialization-tab-picture-item__wrapper-text {
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: $white;
  display: flex;
  flex-direction: column;
  padding: vw(32);
  width: 57.74%;
  border-radius: 0 vw(8) 0 0;

  @media (max-width: 1023px) {
    padding: 12px;
    width: 81.1%;
    border-radius: 0 8px 0 0;
  }
}

.industry-specialization-tab-picture-item__text {
  text-transform: uppercase;
  font-size: vw(40);
  font-weight: 700;
  color: $color2;
  margin-bottom: vw(8);

  @media (max-width: 1023px) {
    font-size: 16px;
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.industry-specialization-tab-picture-item__count {
  font-size: vw(16);
  color: $mainColor;

  @media (max-width: 1023px) {
    font-size: 14px;
  }
}
