.web {
  position: relative;
  padding: 0 0 vw(156) 0;

  @media (max-width: 1023px) {
    padding: 0 0 120px;
  }
  &-tentacle {
    position: absolute;
    z-index: 1;
    left: 0;
    top: vw(-221);
    width: vw(1226);
    pointer-events: none;
  }
  &-data {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    bottom: 0;
    z-index: 2;
    padding-top: vw(256);
    box-sizing: border-box;
  }
  &-data-in {
    width: 100%;
    padding-left: vw(50);
    padding-right: vw(150);
    box-sizing: border-box;
    height: 300px;
  }
  &-subtitles {
    position: relative;
    height: vw(30);
    margin-bottom: vw(30);
    overflow: hidden;
  }
  &-subtitle {
    font-size: vw(30);
    line-height: vw(30);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    @extend %white;
    transform: translateY(-100%);
    opacity: 0;
    transition: transform 0.8s ease;
    &.isActive {
      transition: transform 0.8s ease;
      transform: translateY(0);
      opacity: 1;
    }
  }
  &-titles {
    position: relative;
    height: vw(100);
    margin-bottom: vw(30);
    overflow: hidden;
  }
  &-title {
    font-weight: bold;
    font-size: vw(120);
    line-height: vw(100);
    text-transform: uppercase;
    position: absolute;
    text-shadow: 8px -1px 0px #121423;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    @extend %white;
    transform: translateY(-100%);
    opacity: 0;
    transition: transform 0.8s ease;
    &.isActive {
      transition: 0.8s transform 0.2s ease;
      transform: translateY(0);
      opacity: 1;
    }
  }
  &-texts {
    width: vw(810);
    height: vw(300);
    position: relative;
    overflow: hidden;
  }
  &-text {
    font-size: vw(30);
    line-height: vw(50);
    max-height: 100%;
    overflow: auto;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    @extend %white;
    transform: translateY(-100%);
    opacity: 0;
    transition: transform 0.8s ease;
    &.isActive {
      transition: 0.8s transform 0.4s ease;
      transform: translateY(0);
      opacity: 1;
    }
  }
  &-buttons {
    width: vw(272);
    margin-top: vw(70);
    height: vw(85);
    position: relative;
    overflow: hidden;
  }
  &-button {
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateY(-110%);
    opacity: 0;
    transition: transform 0.8s ease;
    &.isActive {
      transition: 0.8s transform 0.6s ease;
      transform: translateY(0);
      opacity: 1;
    }
    height: vw(85);
    @media (max-width: 1023px) {
      height: auto;
    }
  }
  &-right-image {
    height: 100vh;
  }
  &-images {
    padding-left: calc(50% + #{vw(70)});
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 50%;
    }
  }
  &-slider-arrows {
    position: absolute;
    left: 20px;
    right: 20px;
    transform: translateY(-50%);
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &-slider-arrow {
    width: 62px;
    height: 56px;
    padding: 14px;
    background: #121423;
    border: 1px solid #a0a0a0;
    svg {
      fill: #fff;
    }
    &--prev {
      transform: rotate(180deg);
    }
    &--next {
      margin-left: 10px;
    }
  }
  &-slider-image {
    margin-bottom: -15px;
    &-in {
      position: relative;
      padding-top: 56.6%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        object-fit: cover;
      }
    }
  }
  &-slider-title {
    font-weight: 800;
    font-size: 30px;
    line-height: 30px;
    margin-bottom: 20px;
    text-transform: uppercase;
    color: #ffffff;
    position: relative;
    z-index: 2;
  }
  @media (max-width: 1023px) {
    &-slider-title,
    &-slider-text {
      margin-left: 20px;
      margin-right: 10px;
    }
  }

  &-slider-text {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 50px;
    color: #ffffff;
  }
  &-slider-button {
    display: flex;
    justify-content: center;
  }
  @media (max-width: 1023px) {
    &-button {
      position: static;
      opacity: 1;
      transform: translate(0);
      max-width: 277px;
    }
  }
}
@media (max-width: 1023px) {
  body .web .web-slider-title {
    background: transparent !important;
  }
}
